.header {
  display: flex;
	justify-content: center;
	align-items: center;

  position: relative;

  background-color: var(--prime-color);

  ul {
    display: flex;
    align-items: center;

    position: absolute;

    user-select: none;
    cursor: default;

    li {
      display: inline;
      list-style: none;

      margin: 0 15px;
    }
  }

  &--left {
    left: 0;
  }

  &--right {
    right: 0;
  }

  button {
    background-color: transparent;
    border: 0;

    color: #fff;

    cursor: pointer;
  }
}
